@charset "UTF-8";
//NHA-COMMON-RMA-025
.support_rma {
    .rma_list {
        padding:21px 0 40px; max-width:1200px; margin:0 auto;
        ol {
            @extend %clearfix;
        }
        li {
            width:calc(50% - 9px); margin:19px 0 0; float:left; border:1px solid $gray2; overflow:hidden;
            @include borderRadius(15px);
            @include boxSizing;
            &:nth-of-type(2n) {float:right;}
            &:nth-of-type(2n+1) {clear:both;}
        }
        .img_wrap {
            position:relative; overflow:hidden;
            button {
                width:76px; height:76px; position:absolute; top:50%; left:50%; background-image:url(../../images/support/play_btn.png); z-index:1; text-indent:-9999px;
                @include translate(-50%, -50%);
                @include backgroundSoltC;
                &:hover,
                &:focus {
                    & ~ img {@include scale(1.15);}
                }
            }
            img {
                width:100%;
                @include transition;
            }
        }
        .txt_wrap {
            padding:45px 50px 40px; min-height:209px;
            a {
                font-size:26px; font-weight:$bold; color:$gray7;
                @include transition(color, .2s);
                &:hover,
                &:focus {color:$blue;}
            }
            .txt01 {font-size:16px; line-height:25px; color:$gray7; margin:20px 0 0;}
            .txt02 {
                font-size:14px; line-height:22px; color:$gray5; margin:12px 0 0; padding:12px 0 0; border-top:1px solid #e3e3e3;
            }
        }
    }
    @include mob() {
        .rma_list {
            padding:em(40) em(30);
            li {
                width:100%; margin:em(20) 0 0;
                @include borderRadius(1rem);
                &:first-child {margin:0;}
            }
            .img_wrap {
                button {width:76px; height:76px; background-image:url(../../images/support/play_btn.png);}
            }
            .txt_wrap {
                padding:em(60) em(55); min-height:auto;
                a {font-size:em(52);}
                .txt01 {font-size:em(32); line-height:em(42); margin:em(30) 0 0;}
                .txt02 {font-size:em(26); line-height:em(34); margin:em(30) 0 0; padding:em(30) 0 0;}
            }
        }
    }
}

/*220118 start------------------------------------------------------------------------------------------*/
// NHA-COMMON-Technical_Announcements-044
.listable_type01 {
    ul {padding:10px 0 0;}
    li {
        padding:45px 10px; border-bottom:1px solid $gray2; position:relative;
        @extend %clearfix;
        &:last-of-type { border-bottom:none; }
    }
    .list_wrap {
        float:left; display:flex;
        dl {
            &.no_elm { width:100px; }
            &.category_elm { width:200px; }
            &.title_elm { width:580px; }
            &.date_elm { width:180px;  }
        }
        dt { padding:0 20px 0 0; font-size:14px; font-weight:$medium; color:$gray5; line-height:32px; }
        dd {
            padding:5px 20px 0 0; font-size:18px; color:$gray7; font-weight:$regular; line-height:26px;
            @include boxSizing;
        }
    }
    .download_wrap {
        float:right;position:absolute; top:50%; right:10px;
        @include transform(translate(0,-50%));
        a {
            display:block;padding:13px 33px 13px 20px; font-size:13px;
            font-weight:$medium;color:$gray7; text-align:center; border:1px solid $gray2;
            background:url(./../../images/common/download_brochure_icon.png) 84% 50% no-repeat; background-size:10px;
            @include boxSizing;
            @include transition(all, .2s);
            @include borderRadius(100px);
            &:hover, &:focus {
                border-color:$gray7;color:$white;
                background:url(./../../images/common/download_brochure_icon_w.png) 84% 50% no-repeat $gray7;
            }
        }
    }
    @include mob() {
        ul {padding:em(30) em(30) 0;@include boxSizing;}
        li {
            padding:0; margin:em(30) 0 0; border:em(2) solid $gray2;
            @include borderRadius(em(20));
            &:first-of-type { margin:0; }
            &:last-of-type { border:em(2) solid $gray2; }
        }
        .list_wrap {
            padding:em(40) em(50); float:inherit; flex-wrap:wrap; justify-content:space-between;
            dl {
                &.no_elm { width:15%; order:1; }
                &.category_elm { width:55%; order:2; }
                &.title_elm {
                    width:100%; margin:em(35) 0 0;order:4;
                    dd {line-height:em(42);}
                }
                &.date_elm { width:25%; order:3; }
            }
            dt {padding:0 em(20) 0 0; font-size:em(24); font-weight:$regular; line-height:em(32);@include boxSizing;}
            dd {padding:em(15) 0 0; font-size:em(28); font-weight:$regular; line-height:em(26);@include boxSizing;}
        }
        .download_wrap {
            float:inherit; position:static; border-top:em(2) solid $gray2;
            @include transform(translate(0,0));
            a {
                width:100%; padding:em(25) 0; font-size:em(26);font-weight:$bold;
                background:url(./../../images/common/download_brochure_icon.png) 65% 50% no-repeat; background-size:em(15); border:none;
                @include borderRadius(0);
                &:hover, &:focus {background:url(./../../images/common/download_brochure_icon.png) 65% 50% no-repeat;background-size:em(15); color:$gray7;}
            }
        }
    }
}

// NHA-COMMON-Troubleshooting-045
.listable_type01.troubleshooting_type {
    ul {padding:0;}
    .list_wrap {
        dl {
            &.no_elm { width:90px; }
            &.type_elm { width:135px; }
            &.released_to_elm { width:125px; }
            &.title_elm {
                width:410px;
                dd {
                    padding:5px 70px 0 0;
                    height:35px;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:1;-webkit-box-orient:vertical;
                }
            }
            &.date_elm { width:260px;  }
        }
    }
    .learn_more_wrap {
        float:right;position:absolute; top:50%; right:10px;
        @include transform(translate(0,-50%));
        a {
            display:block;padding:13px 20px; font-size:15px;
            font-weight:$regular;color:#4d4e4f; text-align:center; border:1px solid $gray2;
            @include boxSizing;
            @include transition(all, .2s);
            @include borderRadius(100px);
            &:hover, &:focus {border-color:$gray7;}
        }
    }
    @include mob() {
        ul {padding:em(30) em(30) 0;@include boxSizing;}
        .list_wrap {
            padding:em(40) em(50); float:inherit; flex-wrap:wrap; justify-content:space-between;
            dl {
                order:inherit;
                &.no_elm { width:25%; }
                &.type_elm { width:37%; }
                &.released_to_elm {
                    width:auto;
                    dd {}
                }
                &.title_elm {
                    width:100%;
                    dd {height:em(80);padding:0;-webkit-line-clamp:2;}
                }
                &.date_elm {width:100%; order:0;margin:em(35) 0 0;}
            }
        }
        .learn_more_wrap {
            float:inherit; position:static; border-top:em(2) solid $gray2;
            @include transform(translate(0,0));
            a {
                width:100%; padding:em(25) 0; font-size:em(26);font-weight:$bold;
                @include borderRadius(0);
                &:hover, &:focus {color:$gray7;}
            }
        }
    }
}
/*220118 end------------------------------------------------------------------------------------------*/























