@charset "UTF-8";

.mypage_container {
  margin:80px 0 0;
  .overview {
    padding:45px 0 50px; background-color:$gray7;
    h2 {font-size:40px; margin:0 0 38px; font-weight:$regular; color:$gold2; font-family:$bebasNeue;}
    .title {
      margin:0 0 46px; line-height:28px;
      @extend %clearfix;
      h3 {font-size:28px; font-weight:$medium; color:$white; float:left;}
      span {font-size:14px; color:$gray3; float:right; display:block; position:relative; padding:0 28px 0 0;}
      .btn_refresh {
        position:absolute; top:50%; right:0; background-image:url(../../images/common/reset_icon.png);
        @include backgroundSoltC();
        @include textBackground(20px, 20px);
        @include translate(0, -50%);
      }
    }
    .conts {
      a {
        font-size:20px; padding:0 20px 0 0; margin:0 0 20px; font-weight:$medium; display:inline-block; color:$gray7; position:relative;
        &:hover, &:focus {color:$blue5;}
        @include transition();
        &:after {
          width:18px; height:18px; position:absolute; top:1px; right:0; background-image:url(../../images/common/icon_arrow_tit.png); content:"";
          @include backgroundSoltC();
        }
      }
      .cont03 {
        display:flex; justify-content:space-between;
        a {
          color:$white;
          &:after {
            background-image:url(../../images/common/icon_arrow_tit_w.png);
          }
        }
        .status_wrap {
          width:100%;
          &:first-child {width:74%; margin:0 11px 0 0;}
          div {
            padding:31px 12px 16px; background-color:$white; border-radius:15px; box-sizing:border-box;
            @extend %clearfix;
            em {font-size:14px; font-weight:$medium; display:block; margin:0 0 10px; color:$gray5}
            strong {font-size:23px; font-weight:$bold; color:$gray7; display:block;}
            > p {
              width:90px; padding:0 0 0 18px; float:left; box-sizing:border-box; position:relative;
              &:after {
                width:1px; height:41px; background-color:$gray2; position:absolute; top:50%; right:-10px; content:"";
                @include translate(0, -50%);
              }
            }
            ul {float:left; width:calc(100% - 90px); box-sizing:border-box; padding:0 5px; display:flex; flex-wrap:wrap; text-align:center; justify-content:center;}
            li {
              width:90px; margin:0 1px 15px 0;
              em {
                &.status_type01 {color:$gray5;}
                &.status_type02 {color:$green1;}
                &.status_type03 {color:$green;}
                &.status_type04 {color:$blue;}
                &.status_type05 {color:$red;}
              }
            }
          }
        }
      }
    }
  }
  #contents {
    .title_wrap {
      max-width:1200px; margin:0 auto 35px;
      h3 {font-size:30px; font-weight:$regular; color:$gray7; font-family:$bebasNeue;}
    }
    .shortcut_section {
      max-width:1200px; margin:0 auto; padding:75px 0 80px;
      ul {
        width:100%; display:flex;
        li {
          width:14.28%; border:1px solid $gray2; box-sizing:border-box; margin:0 0 0 -1px;
          &:first-child {margin:0;}
          &.menu_icon01 a {
            &:after {background-image:url(../../images/common/shortcut_icon01.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon01_on.png)}
          }
          &.menu_icon02 a {
            &:after {background-image:url(../../images/common/shortcut_icon02.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon02_on.png)}
          }
          &.menu_icon03 a {
            &:after {background-image:url(../../images/common/shortcut_icon03.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon03_on.png)}
          }
          &.menu_icon04 a {
            &:after {background-image:url(../../images/common/shortcut_icon04.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon04_on.png)}
          }
          &.menu_icon05 a {
            &:after {background-image:url(../../images/common/shortcut_icon05.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon05_on.png)}
          }
          &.menu_icon06 a {
            &:after {background-image:url(../../images/common/shortcut_icon06.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon06_on.png)}
          }
          &.menu_icon07 a {
            &:after {background-image:url(../../images/common/shortcut_icon07.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon07_on.png)}
          }
        }
        a {
          width:100%; min-height:169px; padding:22px 25px 30px 30px; box-sizing:border-box; display:block; position:relative;
          @include transition();
          &:after {
            width:68px; height:68px; position:absolute; bottom:25px; right:25px; content:"";
            @include backgroundSolt();
          }
          strong {
            font-size:16px; line-height:20px; padding:0 0 5px; position:relative; font-weight:$medium; color:$gray7; display:block;
            @include transition();
            &:after {
              width:16px; height:16px; position:absolute; bottom:-18px; left:0; background-image:url(../../images/main/icon_arrow_s.png); content:"";
              @include backgroundSoltC();
              @include transition();
            }
          }
          &:hover, &:focus {
            background-color:$blue5; border-radius:20px;
            @include scale(1.05);
            @include boxShadow(0 0 20px rgba(15, 13, 13, 0.15));
            strong {
              color:$white;
              &:after {background-image:url(../../images/main/icon_arrow_s_on.png)}
            }
          }
        }
      }
    }
    .ordered_section {
      padding:75px 0 80px; background-color:$gray1; overflow-x:hidden;
      .cont {
        margin:80px 0 0;
        &:first-child {margin:0}
      }
      .slide_wrap {max-width:1200px; margin:0 auto; overflow:visible; box-sizing:border-box;}
      .order_slide_type01 {
        .swiper-slide {max-width:410px; margin:0 20px 0 0;}
        .item {
          padding:45px 40px 40px; background-color:$white; border-radius:15px; border:1px solid $gray2; box-sizing:border-box;
          @include transition();
          .info {
            position:relative; padding:0 0 26px;
            &.info_type02 {padding:0 0 40px;}
            > a {
              font-size:30px; color:$gray7; font-weight:$medium;
              &:hover {text-decoration:underline;}
            }
            div {
              padding:27px 0 0;
              dl {
                font-size:17px; line-height:22px; margin:4px 0 0; padding:0 0 0 90px; position:relative;
                &:first-child {margin:0;}
                dt {position:absolute; top:0; left:0; color:$gray5;}
                dd {color:$gray7;}
                strong {font-weight:$medium; vertical-align:middle;}
                em {
                  font-weight:$medium; vertical-align:middle;
                  &.state_type01 {color:$green;}
                  &.state_type02 {color:$red;}
                  &.state_type03 {color:$blue;}
                  &.state_type04 {color:$green1;}
                }
              }
            }
            .btn_detail {
              font-size:15px; line-height:36px; width:123px; border-radius:30px; text-align:center; border:1px solid $gray2; color:$gray6; position:absolute; top:-4px; right:0;
              @include transition();
              &:hover, &:focus {color:$white; border-color:$blue5; background-color:$blue5}
            }
          }
          .details {
            padding:30px; border-top:1px solid $gray2; position:relative;
            img {width:100%;}
            .options {
              position:absolute; top:23px; left:0; width:100%; height:calc(100% - 50px); background-color:$white; padding-left:0;
              div {
                margin:25px 0 0;
                &:first-child {margin:0;}
                strong {font-size:15px; font-weight:$medium; color:$gray5;}
                ul {margin:15px 0 0;}
                li {
                  font-size:15px; line-height:20px; margin:7px 0 0; padding:0 0 0 20px; color:$gray7; position:relative;
                  &:first-child {margin:0;}
                  &:before {width:3px; height:3px; background-color:$gray7; content:""; border-radius:100%; position:absolute; top:9px; left:6px;}
                }
              }
            }
          }
          .btn_reorder {
            font-size:16px; line-height:46px; border-radius:10px; display:block; text-align:center; font-weight:$bold; color:$blue5; background-color:$blue1;
            @include transition();
            &:hover, &:focus {background-color:$blue5; color:$white;}
          }
          &:hover {
            @include boxShadow(0 10px 35px rgba(18, 19, 20, 0.1));
          }
        }
      }
      .order_slide_type02 {
        .swiper-slide {max-width:298px; margin:0 16px 0 0;}
        .item {
          padding:30px; background-color:$white; border-radius:15px; border:1px solid $gray2; box-sizing:border-box;
          @include transition();
          .info {
            > a {
              font-size:23px; color:$gray7; font-weight:$medium; text-overflow:ellipsis; white-space:nowrap; overflow:hidden; display:block;
              &:hover {text-decoration:underline;}
            }
            div {
              padding:25px 0 20px;
              dl {
                font-size:17px; line-height:22px; margin:4px 0 0; padding:0 0 0 90px; position:relative;
                &:first-child {margin:0;}
                dt {position:absolute; top:0; left:0; color:$gray5;}
                dd {color:$gray7;}
                strong {font-weight:$medium; vertical-align:middle;}
                em {
                  font-weight:$medium; vertical-align:middle;
                  &.state_type01 {color:$green;}
                  &.state_type02 {color:$red;}
                  &.state_type03 {color:$blue;}
                  &.state_type04 {color:$green1;}
                }
              }
            }
            img {width:100%}
            .btn_reorder {
              font-size:14px; line-height:38px; border-radius:10px; margin:25px 0 0; text-align:center; font-weight:$bold; display:block; color:$blue5; background-color:$blue1;
              @include transition();
              &:hover, &:focus {background-color:$blue5; color:$white; text-decoration:none}
            }
          }
          &:hover {
            @include boxShadow(0 10px 35px rgba(18, 19, 20, 0.1));
          }
        }
      }
      .order_slide_type03 {
        .swiper-slide {max-width:298px; margin:0 16px 0 0;}
        .item {
          padding:30px; background-color:$white; border-radius:15px; border:1px solid $gray2; box-sizing:border-box;
          @include transition();
          .info {
            > strong {
              font-size:23px; color:$gray7; font-weight:$medium;
            }
            div {
              padding:25px 0 20px; min-height:125px;
              dl {
                font-size:17px; line-height:22px; margin:4px 0 0; padding:0 0 0 90px; position:relative;
                &:first-child {margin:0;}
                dt {position:absolute; top:0; left:0; color:$gray5;}
                dd {color:$gray7;}
                strong {font-weight:$medium; vertical-align:middle;}
                em {
                  font-weight:$medium; vertical-align:middle;
                  &.state_type01 {color:$green;}
                  &.state_type02 {color:$red;}
                  &.state_type03 {color:$blue;}
                  &.state_type04 {color:$green1;}
                }
              }
            }
            img {width:100%}
          }
          &:hover {
            @include boxShadow(0 10px 35px rgba(18, 19, 20, 0.1));
          }
        }
      }
    }
  }

  @include mob(){
    margin:em(124) 0 0;
    .overview {
      padding:em(65) em(30);
      h2 {font-size:em(64); margin:0 0 em(70);}
      .title {
        margin:0 0 em(50); line-height:em(46);
        h3 {font-size:em(46); font-weight:$bold;}
        span {font-size:em(24); padding:0 em(38) 0 0;}
        .btn_refresh {
          width:em(28); height:em(28); background-image:url(../../images/common/reset_icon_m.png);
        }
      }
      .conts {
        a {
          font-size:em(36); padding:0 em(38) 0 0; margin:0 0 em(25); font-weight:$bold;
          @include transition();
          &:after {
            width:18px; height:18px; position:absolute; top:1px; right:0; background-image:url(../../images/common/icon_arrow_tit.png); content:"";
            @include backgroundSoltC();
          }
        }
        .cont03 {
          display:block;
          a {
            color:$gray7;
            &:after {
              width:em(32); height:em(32); position:absolute; top:50%; right:0; background-image:url(../../images/common/icon_arrow_tit_m.png); content:"";
              @include backgroundSoltC();
              @include translate(0, -50%);
            }
          }
          .status_wrap {
            background-color:$white; border-radius:em(20); margin:em(20) 0 0; padding:em(50); box-sizing:border-box;
            &:first-child {width:100%; margin:0;}
            div {
              padding:0; background-color:transparent; position:relative; border-radius:0;
              em {font-size:em(22); font-weight:$bold; margin:0 0 em(35);}
              strong {font-size:em(48); font-weight:$bold;}
              > p {
                width:100%; text-align:right; padding:0; position:absolute; top:em(-80); right:0; font-size:0;
                &:after {display:none;}
                em {margin:0; display:inline-block; vertical-align:bottom;}
                strong {display:inline-block; vertical-align:bottom; padding:0 0 0 em(40); position:relative; top:em(5)}
              }
              ul {width:100%; padding:em(40) 0 0; border-top:em(2) solid $gray2;}

              .status_order {
                li {width:em(140); margin:0;}
              }
              .status_RMA {
                display:block; overflow:hidden;
                li {
                  float:left; width:33.333%; overflow:hidden; text-align:center; margin:em(30) 0 0;
                  &:nth-of-type(3n+1) {clear:both;}
                  &:nth-of-type(3n+1) p {float:left;}
                  &:nth-of-type(3n+2) p {margin:0 auto;}
                  &:nth-of-type(3n+3) p {float:right;}
                  &:nth-of-type(1), &:nth-of-type(2), &:nth-of-type(3) {margin:0;}
                  &:nth-of-type(1) p, &:nth-of-type(2) p, &:nth-of-type(3) p {padding-top:0;}
                }
                p {width:98%; padding:em(30) 0 0}
              }
            }
          }
        }
      }
    }
    #contents {
      .title_wrap {
        margin:0 auto em(50); padding:0 em(30);
        h3 {font-size:em(64);}
      }
      .shortcut_section {
        padding:em(90) 0 em(90) em(30);
        .menu_list_wrap {overflow-x:auto; position:relative; padding:em(240) 0 0;}
        ul {
          width:max-content; padding:0 em(30) 0 0; position:absolute; top:0; left:0; box-sizing:border-box;
          li {
            width:em(240); margin:0 0 0 em(14); border-radius:em(10);
          }
          a {
            min-height:em(236); padding:em(30);
            &:after {
              width:em(96); height:em(96); bottom:em(28); right:em(28); background-size:cover !important;
            }
            strong {
              font-size:em(24); line-height:em(32); padding:0 0 em(35);
              &:after {
                width:em(20); height:em(20); bottom:0; left:0; background-image:url(../../images/main/icon_arrow_s_m.png);
              }
            }
          }
        }
        .title_wrap {padding:0;}
      }
      .ordered_section {
        padding:em(90) 0;
        .cont {margin:em(120) 0 0;}
        .slide_wrap {padding:0 0 0 em(30)}
        .order_slide_type01 {
          .swiper-slide {max-width:em(590); margin:0 em(23) 0 0;}
          .item {
            padding:em(50); border-radius:em(20);
            .info {
              //margin:0 0 em(25);
              padding:0 0 em(25);
              &.info_type02 {padding:0 0 em(40);}
              margin:0;
              > a {font-size:em(48); font-weight:$bold;}
              div {
                padding:em(50) 0 0;
                dl {
                  font-size:em(30); line-height:em(42); margin:em(6) 0 0; padding:0 0 0 em(140);
                  strong {font-weight:$bold}
                  em {font-weight:$bold;}
                }
              }
              .btn_detail {font-size:em(24); line-height:em(60); width:em(192); border-radius:em(50); top:em(-6); font-weight:$bold; color:$gray7}
            }
            .details {
              padding:em(30) 0;
              .options {
                top:em(30); height:calc(100% - 3rem);
                div {
                  margin:em(35) 0 0;
                  strong {font-size:em(30); font-weight:$bold;}
                  ul {margin:em(25) 0 0;}
                  li {
                    font-size:em(30); line-height:em(38); margin:em(5) 0 0; padding:0 0 0 em(35);
                    &:before {width:em(6); height:em(6); top:em(14); left:em(14);}
                  }
                }
              }
            }
            .btn_reorder {font-size:em(28); line-height:em(84); border-radius:em(10);}
          }
        }
        .order_slide_type02 {
          .swiper-slide {max-width:em(590); margin:0 em(23) 0 0;}
          .item {
            padding:em(50); border-radius:em(20);
            .info {
              > a {font-size:em(48); font-weight:$bold;}
              div {
                padding:em(50) 0 em(25);
                dl {
                  font-size:em(30); line-height:em(42); margin:em(6) 0 0; padding:0 0 0 em(140);
                  strong {font-weight:$bold}
                  em {font-weight:$bold;}
                }
              }
              .btn_reorder {font-size:em(28); line-height:em(84); border-radius:em(10); margin:em(30) 0 0;}
            }
          }
        }
        .order_slide_type03 {
          .swiper-slide {max-width:em(590); margin:0 em(23) 0 0;}
          .item {
            padding:em(50); border-radius:em(20);
            .info {
              > strong {font-size:em(48); font-weight:$bold;}
              div {
                padding:em(40) 0; min-height:em(230);
                dl {
                  font-size:em(30); line-height:em(42); margin:em(6) 0 0; padding:0 0 0 em(160);
                  strong {font-weight:$bold}
                  em {font-weight:$bold;}
                }
              }
            }
          }
        }
      }
    }
  }
}

// NHA-COMMON-CANCEL(P)-039
.popup.cancel_reason_popup {
  .popup_content {
    max-width:460px; text-align:center; background-color:$white; border-radius:24px;
    .title {
      padding:56px 0 0;
      strong {font-size:30px; color:$gray7; font-weight:$medium;}
    }
  }
  .cancel_reason_form {padding:36px 40px 50px;}
  .btn_type01 {
    font-size:16px; line-height:58px; font-weight:$bold; background-color:$blue5; border:1px solid $blue5; border-radius:8px; display:block; box-sizing:border-box; color:$white; cursor:pointer; margin:40px 0 0;
    @include transition();
    &:hover, &:focus {background-color:$blue6; border-color:$blue6;}
    &.btn_disabled {
      background-color:$blue4; border-color:$blue4; cursor:default;
      &:hover, &:focus {background-color:$blue4; border-color:$blue4;}
    }
  }
  .btn_popup_close {width:20px; height:20px; top:28px; right:26px; background-image:url(../../images/common/close_x_btn.png);}

  @include mob() {
    .popup_content {
      max-width:100%; margin:0 em(30); border-radius:em(24);
      .title {
        padding:em(112) 0 0;
        strong {font-size:em(48); font-weight:$bold;}
      }
    }
    .cancel_reason_form {padding:em(50) em(60) em(80);}
    .btn_type01 {font-size:em(32); line-height:em(100); border:em(2) solid $blue5; border-radius:em(16); margin:em(80) 0 0;}
    .btn_popup_close {width:em(72); height:em(72); background-image:url(../../images/common/close_x_btn_m.png); right:em(22); top:em(22);}
  }
}

