          padding:0 em(36) 0 em(16); background-color:$gray1; background-image:url(../../images/my_menu/order_popup_ico.png); border-radius:em(22); font-size:em(22); line-height:em(36); position:absolute; top:-46%; right:0; background-size:em(12) em(12);
          @include backgroundSolt(92%, 50%);
        }
        > div {width: 20%; text-align:center;}
        p {font-size:em(24); color:$gray5;}
        .date {font-size:em(22); color:$gray7;}
        .ico_wrap {
          width:em(90); height:em(90); background-color:$gray2; border-radius:50%; text-align:center; display:inline-block; margin:em(20) 0; line-height:em(90); position:relative;
          &.active {
            background-color:$blue5;
            & ~ p {color:$blue5;}
          }
          img {width:55.56%; vertical-align:middle;}
        }
      }
    }
  }
}

